

.load{transform:translate(-50%, -50%);
	margin-left:46%;
  /*change these sizes to fit into your project*/
  width:100px;
  height:100px;
}
.load hr{border:0;margin:0;width:40%;height:40%;position:absolute;border-radius:50%;animation:spin 2s ease infinite}

.load :first-child{background:#ff0066;animation-delay:-1.5s}
.load :nth-child(2){background:#33ccff;animation-delay:-1s}
.load :nth-child(3){background:#a84cf0;animation-delay:-0.5s}
.load :last-child{background:#ff8c33}

@keyframes spin{
  0%,100%{transform:translate(0)}
  25%{transform:translate(160%)}
  50%{transform:translate(160%, 160%)}
  75%{transform:translate(0, 160%)}
}
@-webkit-keyframes spin{
	  0%,100%{transform:translate(0)}
  25%{transform:translate(160%)}
  50%{transform:translate(160%, 160%)}
  75%{transform:translate(0, 160%)}
}
@-moz-keyframes spin{
	  0%,100%{transform:translate(0)}
  25%{transform:translate(160%)}
  50%{transform:translate(160%, 160%)}
  75%{transform:translate(0, 160%)}
}